.login {
  background: #000;
  height: 100%;
}
.login .login-header {
  width: 100%;
  height: 100%;
  display: flex;
  height: 100px;
  align-items: center;
  background: #000;
  color: #ffff;
}
.login .login-header img {
  width: 50px;
  height: 45px;
}
.login .login-header h2 {
  font-size: 28px;
  margin-left: 20px;
  color: #fff;
}
.login .login-content {
  width: 350px;
  height: 300px;
  background: #fff;
  margin: 50px auto;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 1px 1px 8px #cecece;
}
.login .login-content h2 {
  font-size: 23px;
  text-align: center;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
  border-radius: 3px;
}
